/**
 * Example 10: Text Rendering
 * Showcases different ways to render text in PixiJS
 * Features: mechanical scrolling counter, BitmapText, styled text, and effects
 */
import { setupMoxi } from 'moxi';
import * as PIXI from 'pixi.js';
import { Assets } from 'pixi.js';
import { ASSETS } from '../assets-config';

export async function initTextRendering() {
  const root = document.getElementById('canvas-container');
  if (!root) throw new Error('App element not found');

  const { scene, engine, renderer } = await setupMoxi({
    hostElement: root,
    renderOptions: {
      width: 1280,
      height: 720,
      backgroundColor: 0x1a1a2e
    }
  });

  // Load fonts
  await Assets.load([ASSETS.KENNEY_FUTURE_FONT, ASSETS.KENNEY_BLOCKS_FONT]);

  // Generate bitmap font from TTF using BitmapFont.install
  PIXI.BitmapFont.install({
    name: 'KenneyBlocks',
    style: {
      fontFamily: 'Kenney Blocks',
      fontSize: 48,
      fill: 0xffffff
    }
  });

  // Create title
  const title = new PIXI.Text({
    text: 'Text Rendering Showcase',
    style: {
      fontSize: 48,
      fill: 0xffffff,
      fontWeight: 'bold'
    },
    resolution: 2
  });
  title.anchor.set(0.5, 0);
  title.position.set(640, 20);
  scene.addChild(title);

  // ===== MECHANICAL COUNTER =====
  const counterLabel = new PIXI.Text({
    text: 'Mechanical Counter:',
    style: {
      fontSize: 24,
      fill: 0xaaaaaa
    },
    resolution: 2
  });
  counterLabel.position.set(100, 100);
  scene.addChild(counterLabel);

  // Create mechanical counter
  const mechanicalCounter = createMechanicalCounter(6); // 6 digits
  mechanicalCounter.position.set(400, 90);
  scene.addChild(mechanicalCounter);

  // Animate counter
  let currentValue = 0;
  const targetValue = 999999;
  const incrementSpeed = 1234; // Increase by this much per frame

  engine.ticker.add(() => {
    if (currentValue < targetValue) {
      currentValue = Math.min(currentValue + incrementSpeed, targetValue);
      updateMechanicalCounter(mechanicalCounter, currentValue);
    }
  });

  // ===== BITMAP TEXT =====
  const bitmapLabel = new PIXI.Text({
    text: 'BitmapText (High Performance):',
    style: {
      fontSize: 24,
      fill: 0xaaaaaa
    },
    resolution: 2
  });
  bitmapLabel.position.set(100, 200);
  scene.addChild(bitmapLabel);

  // BitmapText generated from TTF using BitmapFont.install
  const bitmapText = new PIXI.BitmapText({
    text: 'FAST BITMAP TEXT',
    style: {
      fontFamily: 'KenneyBlocks',
      fontSize: 36
    }
  });
  bitmapText.tint = 0x4ECDC4;
  bitmapText.position.set(100, 240);
  scene.addChild(bitmapText);

  // Frequently updating counter (shows BitmapText performance)
  let frameCount = 0;
  const fpsCounter = new PIXI.BitmapText({
    text: 'FPS: 60',
    style: {
      fontFamily: 'KenneyBlocks',
      fontSize: 24
    }
  });
  fpsCounter.tint = 0x00ff00;
  fpsCounter.position.set(450, 250);
  scene.addChild(fpsCounter);

  // Update FPS counter every frame (demonstrates BitmapText performance)
  let lastTime = performance.now();
  let fps = 60;
  engine.ticker.add(() => {
    frameCount++;
    const currentTime = performance.now();
    const delta = currentTime - lastTime;

    if (delta >= 1000) {
      fps = Math.round((frameCount * 1000) / delta);
      fpsCounter.text = `FPS: ${fps}`;
      frameCount = 0;
      lastTime = currentTime;
    }
  });

  // ===== STYLED TEXT EXAMPLES =====
  const stylesLabel = new PIXI.Text({
    text: 'Text Styles (Rich Formatting):',
    style: {
      fontSize: 24,
      fill: 0xaaaaaa
    },
    resolution: 2
  });
  stylesLabel.position.set(100, 330);
  scene.addChild(stylesLabel);

  // Stroke text
  const strokeText = new PIXI.Text({
    text: 'Bold Stroke',
    style: {
      fontSize: 32,
      fill: 0xffffff,
      stroke: { color: 0xff6b35, width: 4 },
      padding: 4
    },
    resolution: 2
  });
  strokeText.position.set(100, 370);
  scene.addChild(strokeText);

  // Gradient text (using array of colors)
  const gradientFill = new PIXI.FillGradient(0, 0, 0, 32 * 1.7);
  gradientFill.addColorStop(0, 0xff00ff);
  gradientFill.addColorStop(1, 0x00ffff);

  const gradientText = new PIXI.Text({
    text: 'Gradient Fill',
    style: {
      fontSize: 32,
      fill: gradientFill
    },
    resolution: 2
  });
  gradientText.position.set(100, 420);
  scene.addChild(gradientText);

  // Shadow text
  const shadowText = new PIXI.Text({
    text: 'Drop Shadow',
    style: {
      fontSize: 32,
      fill: 0xffffff,
      dropShadow: {
        alpha: 0.8,
        angle: Math.PI / 4,
        blur: 4,
        color: 0x000000,
        distance: 5
      },
      padding: 10
    },
    resolution: 2
  });
  shadowText.position.set(100, 470);
  scene.addChild(shadowText);

  // Multi-line wrapped text
  const wrappedText = new PIXI.Text({
    text: 'This is an example of word-wrapped text. It automatically breaks into multiple lines when it reaches the specified width.',
    style: {
      fontSize: 18,
      fill: 0xdddddd,
      wordWrap: true,
      wordWrapWidth: 400,
      lineHeight: 24
    },
    resolution: 2
  });
  wrappedText.position.set(100, 530);
  scene.addChild(wrappedText);

  // Outline text (stroke only, no fill)
  const outlineText = new PIXI.Text({
    text: 'OUTLINE',
    style: {
      fontSize: 48,
      fill: 'transparent',
      stroke: { color: 0x00ff00, width: 3 },
      padding: 4
    },
    resolution: 2
  });
  outlineText.position.set(600, 370);
  scene.addChild(outlineText);

  // Animated tint example
  const animatedText = new PIXI.Text({
    text: 'ANIMATED',
    style: {
      fontSize: 36,
      fill: 0xffffff,
      fontWeight: 'bold'
    },
    resolution: 2
  });
  animatedText.position.set(600, 450);
  scene.addChild(animatedText);

  // Animate the tint color
  let hue = 0;
  engine.ticker.add(() => {
    hue = (hue + 1) % 360;
    animatedText.tint = hslToHex(hue, 100, 50);
  });

  // Initialize and start
  scene.init();
  engine.start();

  console.log(' Text Rendering example loaded!');
  console.log('    Mechanical counter with scrolling digits');
  console.log('    BitmapText with live FPS counter');
  console.log('    Rich text styles: gradients, shadows, strokes');
  console.log('    Animated color-changing text');
}

/**
 * Convert HSL to Hex color
 */
function hslToHex(h: number, s: number, l: number): number {
  l /= 100;
  const a = s * Math.min(l, 1 - l) / 100;
  const f = (n: number) => {
    const k = (n + h / 30) % 12;
    const color = l - a * Math.max(Math.min(k - 3, 9 - k, 1), -1);
    return Math.round(255 * color);
  };
  return (f(0) << 16) | (f(8) << 8) | f(4);
}

/**
 * Creates a mechanical counter with scrolling digit animation
 */
function createMechanicalCounter(numDigits: number): PIXI.Container {
  const container = new PIXI.Container();
  const digitContainers: PIXI.Container[] = [];
  const digitWidth = 50;
  const digitHeight = 80;

  for (let i = 0; i < numDigits; i++) {
    const digitContainer = new PIXI.Container();
    digitContainer.x = i * (digitWidth + 10);

    // Create background panel
    const bg = new PIXI.Graphics();
    bg.rect(0, 0, digitWidth, digitHeight);
    bg.fill(0x000000);
    bg.stroke({ color: 0x444444, width: 2 });
    digitContainer.addChild(bg);

    // Create mask for digit scrolling
    const mask = new PIXI.Graphics();
    mask.rect(5, 5, digitWidth - 10, digitHeight - 10);
    mask.fill(0xffffff);
    digitContainer.addChild(mask);

    // Create scrolling digits container (0-9 vertically)
    const scrollContainer = new PIXI.Container();
    scrollContainer.mask = mask;

    for (let digit = 0; digit <= 9; digit++) {
      const text = new PIXI.Text({
        text: digit.toString(),
        style: {
          fontSize: 48,
          fill: 0x00ff00,
          fontFamily: 'monospace',
          fontWeight: 'bold'
        },
        resolution: 3
      });
      text.anchor.set(0.5);
      text.position.set(digitWidth / 2, digit * digitHeight + digitHeight / 2);
      scrollContainer.addChild(text);
    }

    digitContainer.addChild(scrollContainer);
    // Store data using a custom property
    (digitContainer as any)._counterData = { scrollContainer, currentValue: 0, targetValue: 0 };
    digitContainers.push(digitContainer);
    container.addChild(digitContainer);
  }

  (container as any)._counterData = { digitContainers };
  return container;
}

/**
 * Updates the mechanical counter to display a new value with animation
 */
function updateMechanicalCounter(counter: PIXI.Container, value: number): void {
  const { digitContainers } = (counter as any)._counterData as { digitContainers: PIXI.Container[] };
  const valueStr = value.toString().padStart(digitContainers.length, '0');

  for (let i = 0; i < digitContainers.length; i++) {
    const digitContainer = digitContainers[i];
    const counterData = (digitContainer as any)._counterData;
    const { scrollContainer } = counterData;
    const newTargetValue = parseInt(valueStr[i]);

    // Update target value
    counterData.targetValue = newTargetValue;

    // Animate scroll position
    const digitHeight = 80;
    const targetY = -newTargetValue * digitHeight;
    const currentY = scrollContainer.y;

    // Smooth scroll animation
    const diff = targetY - currentY;
    if (Math.abs(diff) > 0.5) {
      scrollContainer.y += diff * 0.15;
    } else {
      scrollContainer.y = targetY;
    }
  }
}
